}
@media only screen and (min-width: 375px) {
  .menu {
    background-color: var(--color-secundario);
    height: 3rem;
    width: 100%;
    text-align: center;
  }
  
  .menu ul {
    height: 3rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0;
  }
  
  .menu ul a {
    display: block;
    padding: .5rem;
  }
  
  .menu ul li {
    position: relative;
    float: left;
    margin: 0;
    padding: 0;
    height: 3rem;
    width: 100%;
    border: 1px solid var(--color-primario);
  }
  
  .menu ul li:hover {
    background-color: var(--color-primario);
  }
  
  .isActive {
    height: 3rem;
    width: 100%;
    background-color: var(--color-primario);
    border: 1px solid #fff;
    display: flex;
    align-items: center;
  }
  .icon {
    margin-right: .5rem;
    font-size: .8rem;
  }
  .menu ul li a {
    width: 100%;
    height: 3rem;
    color: #fff;
    font-size: .6rem;
    font-weight: 400;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: capitalize;
  }
  
  .menu ul ul {
    display: none;
  }
  
  .menu ul ul li {
    float: none;
    width: 100%;
    height: 3rem;
    background-color: var(--color-secundario);
    margin: 0;
    display: flex;
    align-items: center;
  }
  
  .menu ul li:hover>ul {
    display: block;
  }
}
